                let lastProduct_new = Response.data.works[Response.data.works.length - 1].stop_at;
                if (lastProduct_old !== lastProduct_new) {
                    this.setState({
                        works: products,
                        // products: products,
                        lastProduct: lastProduct_new,
                        orderValue: Response.data.works[Response.data.works.length - 1].stop_at,
                        hasMoreItems: lastProduct_old !== lastProduct_new,
                    })
                }
