import React from 'react'
import './index.less'
import {Avatar, Button, Modal, Spin} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../../config";
import moment from 'moment'
import {Link} from "react-router-dom";

export default class TaskPosted extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
            idActive: 0,
            visibleCommunications: false,
        }
    }

    componentWillMount() {
        console.log(this.state);
        this.getFav()
    }

    getCommunications = (id) => {
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const p = ws[w];
            console.log(p);
            if (p.id === id) {
                // console.log('avl', p);
                return (
                    <div>
                        <h2 className='name'>:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'
                             width='100%'
                        />
                        <h5></h5>
                    </div>
                )
            }
        }
    };
    getFav = () => {
        // ->->
        // const cached = localStorage.works_by_me!==undefined;
        const cached = false;

        if (cached) {
            // 
            this.setState({
                myFavs: JSON.parse(localStorage.works_by_me),
            })
        } else {
            // 
            axios.get(
                baseUrl + '/api/get_works_by_me',
                {
                    params: {
                        token: localStorage.customer_token,
                    }
                })
                .then((Response) => {
                    console.log('works_by_me');
                    this.setState({
                        myFavs: Response.data.works,
                    });
                    // 
                    localStorage.works_by_me = JSON.stringify(Response.data.works)
                });
        }
    };
    showCommunications = (id) => {
        console.log(id);
        this.setState({
            visibleCommunications: true,
            idActive: id,
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "";
            case 2:
                return "";
            case 3:
                return "";
            case 0:
                return "";
            default:
                return "";
        }
    };
    renderFav = () => {
        if (this.state.myFavs.length === 0) {
            return <h2></h2>
        }
        let fs = [];
        const ws = this.state.myFavs;
        for (const w in  ws) {
            const p = ws[w];
            fs.push(
                <div
                    className=
                        {
                            p.stop_at < moment().unix()
                                ? 'cell-work-index cell-disable'
                                : 'cell-work-index'
                        }
                    id={p.id}>
                    <div className='work-cell-row'>
                        {/**/}
                        <span className='work-name'>{p.work_name}</span>
                        {/**/}
                        <span
                            className='work_money'>:{p.work_money !== 0 ? p.work_money : ''}{p.work_tpl === 2 || p.work_tpl === 3 ? '' : ''}</span>
                    </div>
                    <div className='work-cell-row'>
                        <span> </span>
                        <span>:{p.work_person_number}</span>
                    </div>

                    <div className='work-cell-row'>
                        {/**/}
                        <span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>
                        {p.stop_at ?
                            <span>:{
                                moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                    ''
                                    : moment.unix(p.stop_at).format("MM-DD HH:mm")
                            }

                                </span>
                            : null}
                    </div>
                    <div className='work-cell-row'>
                        {/**/}
                        <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}
                                className='avatar'></Avatar>
                        {/**/}
                        <span className='name'>{p.senter_name}</span>
                        {/**/}
                        {
                            //
                            p.stop_at < moment().unix()
                                ? <Button disabled></Button>
                                : null
                        }
                        {/**/}
                        <span className='button-more'>
                            <Link to={`/w/${p.id}`}>
                                    
                                </Link>
                            </span>
                    </div>
                </div>
            )
        }

        return fs
    };

    render() {
        return (
            this.state.myFavs !== undefined
                ?
                <div className='my-favs'>
                    {this.renderFav()}
                    <Modal
                        visible={this.state.visibleCommunications}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        style={{textAlign: 'center'}}
                    >
                        {this.getCommunications(this.state.idActive)}
                    </Modal>
                </div>
                : <Spin/>

        )

    }
}
